
.main_nav_with_arrow {
  padding: 15px 0 0 15px;
  border-bottom: 1px solid #ddd;
  background: #fff;
}
.ul_auto_wrap {
  width: 100%;
  overflow-x: auto;
  box-sizing: border-box;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  text-align: center;
}
.main_nav_with_arrow a {
  position: relative;
  background: #fff;
  padding: 10px 0;
  display: inline-block;
  width: 88px;
  margin-right: 10px;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 14px;
}
.main_nav_with_arrow a.active {
    background: url("img/bg04.png") no-repeat;
    background-size: 100% 100%;
    color: #f66;
    border-color: transparent;
    box-shadow: 0 0 0;
    padding: 10px 0 27px 0;
}
.main_nav_with_arrow a img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 7px;
    height: 40px;
    width: 40px;
}



