
.recommend-firend .user_refer_link_wrap, .recommend-firend .item_list {
  margin: 15px 0;
  padding: 15px;
  border: 1px dashed #f66;
  border-radius: 10px;
  line-height: 24px;
  color: #999;
  font-size: 14px;
}
.recommend-firend .item_list_date {
  line-height: 30px;
  color: #999;
}
.recommend-firend .link_ref {
  font-size: 16px;
  color: #282828;
  line-height: 1.8;
}
.recommend-firend .item_list_money {
  font-size: 20px;
  line-height: 1.8;
}
.recommend-firend .item_list {
  margin-bottom: 10px;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background: url(/img/icon_right.png) right 15px top 50%;
  background-repeat: no-repeat;
  background-size: 7px 12px;
}



