
.grid-row {
  background: #fff;
}
.grid-row .grid-row-item {
    display: block;
    height: 42px;
    margin: 0 15px;
    cursor: pointer;
    line-height: 42px;
    border-bottom: 1px solid #ddd;
    background: url(/images/icons/icon_right.png) right 18px top 50% no-repeat;
    background-size: 7px 12px;
    color: #666;
}
.grid-row .grid-row-item:last-child {
      border-bottom: none;
}



