
.tab-menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  text-align: center;
}
.tab-menu .tab-menu-item {
    display: block;
    height: 45px;
    line-height: 45px;
    background: #eee;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    box-sizing: border-box;
    border-right: 1px solid #d0d0d0;
}
.tab-menu .tab-menu-item.tab-menu-item:last-child {
      border-right: none;
}
.tab-menu .tab-menu-item.active {
      border-right: none;
      background: #fff;
      border-color: #fff;
}



