
.msg-box {
  position: fixed;
  overflow: hidden;
  top: 50%;
  width: 80%;
  z-index: 2;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 500px;
  border-radius: 10px;
  text-align: center;
  background: #fff;
  box-sizing: border-box;
}
.msg-box-title {
  border-bottom: 1px solid #ccc;
  line-height: 40px;
  background-color: #21a1a6;
  color: #000;
  font-weight: bold;
  font-size: 16px;
}
.msg-box-content {
  padding: 20px 40px;
  color: #666;
  line-height: 34px;
  font-size: 16px;
}
.msg-box-btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px #ccc solid;
}
.msg-btn-cancel,
.msg-btn-confirm {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  box-sizing: border-box;
  border-right: 1px #ccc solid;
  line-height: 44px;
  color: #000;
  font-size: 16px;
  width: 50%;
}
.msg-btn-confirm {
  color: #26a2ff;
}



