
.grid-warper {
  width: 100%;
  overflow-y: auto;
}
.m-grid-table {
  width: 100%;
  text-align: center;
  border-spacing: 0;
  border-collapse: collapse;
  line-height: 1.5em;
}
.m-grid-table th {
    border-right: 1px #fff solid;
    background-color: #f66;
    font-weight: bold;
    text-align: center;
    font-size: 16px;
    line-height: 28px;
    white-space: nowrap;
    color: #fff;
    padding: 0 5px;
}
.m-grid-table th:last-child {
      border-right: none;
}
.m-grid-table td {
    border: 1px solid #e5e5e5;
    white-space: nowrap;
}
.table-nodata {
  height: 100px;
  text-align: center;
}



