
.grid-col {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.grid-col .grid-col-item {
    width: 33.33%;
    padding: 15px 0;
    /*flex:1;*/
    box-sizing: border-box;
    border-right: 1px #ccc solid;
    border-bottom: 1px #ccc solid;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    text-align: center;
}
.grid-col .grid-col-item:nth-child(3n) {
      border-right: none;
}
.grid-col .grid-col-item .col-item-red {
      color: #f66;
}



