
.slide-enter,
.slide_back-enter {
  position: absolute;
  width: 100%;
}
.slide-leave,
.slide_back-leave {
  position: absolute;
  width: 100%;
}
.slide-enter-active,
.slide_back-enter-active {
  transition: all 0.3s linear;
}
.slide-leave-active {
  position: absolute;
  transition: all 0.3s linear;
  -webkit-transform: translate(-100%);
          transform: translate(-100%);
}
.slide-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide_back-leave-active {
  position: absolute;
  transition: all 0.3s linear;
  -webkit-transform: translate(100%);
          transform: translate(100%);
}
.slide_back-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}



